<template>
    <div class="wrapper">
        <div class="main">
            <mt-swipe class="banner gap">
                <mt-swipe-item v-for="item in bannerList" :key="item.id">
                    <img src="../../assets/images/temp.png" alt="">
                </mt-swipe-item>
            </mt-swipe>

            <app-tabbar class="gap gray fs12">
                <router-link :to="{name:'consign',query: {type:'query'}}" class="tab-item">
                    <img src="../../assets/images/icon1.png" alt="">
                    <div></div>
                </router-link>
                <router-link :to="{name:'consign',query: {type:'consign'}}" class="tab-item">
                    <img src="../../assets/images/icon2.png" alt="">
                    <div></div>
                </router-link>
                <!-- <router-link :to="{name:'consign',query: {type:'query'}}" class="tab-item">
                    <img src="../../assets/images/icon3.png" alt="">
                    <div></div>
                </router-link> -->
                 <div class="tab-item" @click="call">
                    <img src="../../assets/images/icon4.png" alt="">
                    <div></div>
                </div>
            </app-tabbar>

            <div class="card">
                <div class="card-title">
                    <app-icon class="icon-quanqiuglobal26" type="selected" />
                    /CORE BUSINESS
                </div>
                <div class="flex">
                    <router-link :to="{name:'static',query:{title:'',content:'lianyun',showButton:true}}" class="flex-item"><img src="../../assets/images/transport1.png" alt=""></router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'haiyun',showButton:true}}" class="flex-item"><img src="../../assets/images/transport2.png" alt=""></router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'kongyun',showButton:true}}" class="flex-item"><img src="../../assets/images/transport3.png" alt=""></router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'luyun',showButton:true}}" class="flex-item"><img src="../../assets/images/transport4.png" alt=""></router-link>
                </div>
            </div>
            <div class="card">
                <div class="card-title">
                    <app-icon class="icon-favorite" type="selected" />
                    /SPECIAL SERVICE
                </div>
                <div class="grid gray">
                    <router-link :to="{name:'static',query:{title:'',content:'baoguan',showButton:true}}" class="grid-item">
                        <app-icon class="icon-baoguan" style="color:#f2a306;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'baosui',showButton:true}}" class="grid-item">
                        <app-icon class="icon-service" style="color:#0abab4;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'cangchu',showButton:true}}" class="grid-item">
                        <app-icon class="icon-fq_cangchupeizhi" style="color:#558de6;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'caigou',showButton:true}}" class="grid-item">
                        <app-icon class="icon-purchase" style="color:#fe8751;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'jinrong',showButton:true}}" class="grid-item">
                        <app-icon class="icon-doller" style="color:#788cfc;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                    <router-link :to="{name:'static',query:{title:'',content:'cangdan',showButton:true}}" class="grid-item">
                        <app-icon class="icon-hezuo" style="color:#529fe9;font-size:30px;" size="large"></app-icon>
                        <div></div>
                    </router-link>
                </div>
            </div>
        </div>
        <keep-alive><app-footer></app-footer></keep-alive>
    </div>
</template>

<style lang="scss" scoped>
@import '../../assets/scss/var.scss';

.banner{
    // padding-top: 20px;
    height: 158px;
    // background-color: $themeColor;
    img{
        width: 100%;
        height: 100%;
    }
}
@media screen and (max-width: 360px) {
    .banner{
        height: 197px;
    }
}
@media screen and (max-width: 375px) {
    .banner{
        height: 205px;
    }
}
.tab-item{
    img{
        width: 60%;
    }
}
.card{
    background-color: #fff;
    padding: 10px;
    margin-bottom: 5px;
    font-size: 14px;

    &-title{
        color: #747474;
    }
}
.flex{
    display: flex;
    flex-wrap: wrap;
    &-item{
        flex-basis: 50%;
        padding: 5px;
        img{
            display: block;
        }
    }
}

.grid{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    padding: 5px;

    &-item{
        flex-basis: 33.33%;
        padding: 20px 0;
        border: 1px solid $grayColor;
        margin-top: -1px;
        margin-right: -1px;

        .iconfont{
            font-size: 26px;
        }
    }
}
</style>

<script>
import appHeader from '@/components/header'
import appFooter from '@/components/footer'
import appIcon from '@/components/icon'
import appCell from '@/components/cell'
import appTabbar from '@/components/tabbar'
import { Swipe, SwipeItem } from 'mint-ui'

export default {
    components: {
        appHeader,
        appFooter,
        appIcon,
        appCell,
        appTabbar,
        [Swipe.name]: Swipe,
        [SwipeItem.name]: SwipeItem,
    },
    data() {
        return {
            bannerList: [
                {src:'../../assets/images/temp.png',id:1},
                {src:'../../assets/images/temp.png',id:2}
            ],
        }
    },
    methods: {
        call() {
            window.JSDK.call('18503519655')
        }
    }
}
</script>



